* {
  box-sizing: border-box;
}

html {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 16px;
}

body, h2, p {
  margin: 0;
}

a {
  text-decoration: none;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

li {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__link {
  width: 40px;
  height: 40px;
  display: inline-block;
}

.header__logo {
  width: 100%;
  height: auto;
  display: block;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  min-height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
  transition: color .3s ease-in-out;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.cards-grid {
  justify-content: center;
  gap: 46px 48px;
  width: 100%;
  margin: 0 auto 72px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards-grid {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .cards-grid {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards-grid {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  color: #060b35;
  cursor: pointer;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  max-width: 200px;
  padding-block: 32px 16px;
  padding-inline: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 400;
  transition: all .3s ease-in-out;
  transform: scale(1);
}

.card__image {
  width: 100%;
  max-width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: inherit;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
  transition: color .3s ease-in-out;
}

.card__sku-code {
  color: #616070;
  text-align: left;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 1.4;
}

.card__grade {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: inherit;
  font-size: 10px;
  line-height: 1.4;
}

.card__price-block {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-tag {
  color: #616070;
  font-size: 12px;
  line-height: 1.5;
}

.card__price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 1.125;
}

.card__buy-button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 12px 8px;
  font-weight: 700;
  line-height: 1;
  transition: color .3s ease-in-out;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card:hover .card__buy-button {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
